body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* borderを含めた幅で要素の幅が計算されるように */
*,
*:before,
*:after {
  box-sizing: border-box;
}

.App {
  max-width: calc(
    9px * 35 + 1px * 2
  ); /* a. (マスの幅) * 個数 + (ボーダーの幅) * 本数 */
  text-align: center;
}

/* 要素が真ん中によるようにフレックスボックスを設定 */
#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.dots {
  height: 9px;
  width: 9px;
  background: #0f1d1d;
}

.field {
  display: flex;
  flex-wrap: wrap;
}

.snake {
  background: white;
}

.food {
  background: pink;
}
